* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

#search-box:focus {
  outline: 0;
  border-color: #2196f3;
}

.country-item {
  display: flex;
  gap: 10px;
  align-items: center;
  list-style: none;
}

.country-item:not(:last-child) {
  margin-bottom: 12px;
}

.flag-and-name-wrap {
  display: inline-flex;
  align-items: center;
  gap: 15px;
}

.country-text {
  margin-top: 10px;
}
